import React from "react";
import { Link } from "react-router-dom";

const Card = ({ idApi, name, continent, img }) => {
  return (
    <div>
      <img src={img} alt={name} width="200px" heigth="250px" />
      <h4>{name}</h4>
      <h5>{continent}</h5>
      <br />
      <Link to={"/home/" + idApi}>
        <button>Detalles</button>
      </Link>
    </div>
  );
};

export default Card;
