import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { filterActivity } from "../../redux/actions";

const FilterActivities = ({ setCurrentPage }) => {
  const dispatch = useDispatch();
  const activitys = useSelector((state) =>
    state.countries.map((act) => act.activities)
  );
  const filterActs = activitys.filter((act) => act.length > 0);

  function handleFilter(e) {
    e.preventDefault();
    dispatch(filterActivity(e.target.value));
    setCurrentPage(1);
    // console.log(e.target.value)
  }

  return (
    <div>
      <select onChange={(e) => handleFilter(e)}>
        <option value="none">Seleccione actividad</option>
        {filterActs.map((act) => {
          return (
            <option
              key={act[0].ActivitiesCountries.countryIdApi}
              value={act[0].name}
            >
              {act[0].name}
            </option>
          );
        })}
      </select>
    </div>
  );
};

export default FilterActivities;
