import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { getCountryById } from "../../redux/actions";
import { useEffect } from "react";

const DetailCountries = () => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const detailCountry = useSelector((state) => state.countriesDetail);

  useEffect(() => {
    dispatch(getCountryById(id));
  }, [dispatch, id]);

  console.log(detailCountry, "DETAIL COUNTRY", id);
  return (
    <div>
      <Link to="/home">
        <button>Volver al inicio</button>
      </Link>
      <div>
        <img src={detailCountry.img} alt="flag" />
      </div>
      <div>
        <h1>Pais: {detailCountry.name}</h1>
        <h2>Continente: {detailCountry.continent}</h2>
        <h3>Capital: {detailCountry.capital}</h3>
        <h4>Subregion: {detailCountry.subregion}</h4>
        <h5>Habitantes: {detailCountry.population}</h5>
        <h5>Area: {detailCountry.area}</h5>
        <a href={detailCountry.maps}>Ubicacion en el mapa</a>
      </div>

      <div>
        {detailCountry.activities ? (
          detailCountry.activities.length === 0 ? (
            <div>
              <p>Este pais no tiene actividades creadas</p>
              <p>Desea añadir alguna?</p>
              <Link to="/activities">
                <button>Añadir actividad</button>
              </Link>
            </div>
          ) : (
            detailCountry.activities.map((act) => (
              <div key={act.id}>
                <h2>Actividad {act.id}</h2>
                <h3>{act.name}</h3>
                <h4>Dificultad: Nivel {act.difficulty}</h4>
                <h4>Duracion de actividad: {act.duration} minutos</h4>
                <h4>Temporada: {act.season}</h4>
              </div>
            ))
          )
        ) : (
          <p></p>
        )}
      </div>
    </div>
  );
};

export default DetailCountries;
